import { useEffect, useRef, useState } from "react";
import { motion, AnimatePresence } from "framer-motion";

const TrueFocus3D = ({
  sentence = "CYBER FOCUS PROTOCOL",
  manualMode = false,
  blurAmount = 8,
  borderColor = "#00ff41",
  glowColor = "rgba(0, 255, 65, 0.8)",
  animationDuration = 0.8,
  pauseBetweenAnimations = 1.5,
  theme = "cyberpunk" // cyberpunk, neon, matrix, hologram
}) => {
  const words = sentence.split(" ");
  const [currentIndex, setCurrentIndex] = useState(0);
  const [lastActiveIndex, setLastActiveIndex] = useState(null);
  const [isHovered, setIsHovered] = useState(false);
  const [particles, setParticles] = useState([]);
  const containerRef = useRef(null);
  const wordRefs = useRef([]);
  const [focusRect, setFocusRect] = useState({ x: 0, y: 0, width: 0, height: 0 });
  const particleId = useRef(0);

  // Theme configurations
  const themes = {
    cyberpunk: {
      bg: "linear-gradient(135deg, #0a0a0a 0%, #1a0a2e 50%, #16213e 100%)",
      textColor: "#00ff41",
      accentColor: "#ff0080",
      shadowColor: "0 0 30px rgba(0, 255, 65, 0.5)",
      borderStyle: "solid"
    },
    neon: {
      bg: "linear-gradient(135deg, #000428 0%, #004e92 100%)",
      textColor: "#ff6b6b",
      accentColor: "#4ecdc4",
      shadowColor: "0 0 30px rgba(255, 107, 107, 0.6)",
      borderStyle: "dashed"
    },
    matrix: {
      bg: "linear-gradient(135deg, #000000 0%, #0d1b0d 100%)",
      textColor: "#00ff00",
      accentColor: "#ffffff",
      shadowColor: "0 0 30px rgba(0, 255, 0, 0.7)",
      borderStyle: "dotted"
    },
    hologram: {
      bg: "linear-gradient(135deg, #667db6 0%, #0082c8 50%, #0082c8 100%)",
      textColor: "#ffffff",
      accentColor: "#00d4ff",
      shadowColor: "0 0 30px rgba(0, 212, 255, 0.8)",
      borderStyle: "double"
    }
  };

  const currentTheme = themes[theme] || themes.cyberpunk;

  // Generate particles for active word
  const generateParticles = (rect) => {
    const newParticles = [];
    for (let i = 0; i < 12; i++) {
      newParticles.push({
        id: particleId.current++,
        x: rect.x + Math.random() * rect.width,
        y: rect.y + Math.random() * rect.height,
        vx: (Math.random() - 0.5) * 4,
        vy: (Math.random() - 0.5) * 4,
        life: 1,
        decay: 0.02 + Math.random() * 0.02
      });
    }
    setParticles(prev => [...prev.slice(-20), ...newParticles]);
  };

  // Particle animation loop
  useEffect(() => {
    const animateParticles = () => {
      setParticles(prev => 
        prev.map(particle => ({
          ...particle,
          x: particle.x + particle.vx,
          y: particle.y + particle.vy,
          life: particle.life - particle.decay
        })).filter(particle => particle.life > 0)
      );
    };

    const interval = setInterval(animateParticles, 50);
    return () => clearInterval(interval);
  }, []);

  useEffect(() => {
    if (!manualMode) {
      const interval = setInterval(() => {
        setCurrentIndex((prev) => (prev + 1) % words.length);
      }, (animationDuration + pauseBetweenAnimations) * 1000);

      return () => clearInterval(interval);
    }
  }, [manualMode, animationDuration, pauseBetweenAnimations, words.length]);

  useEffect(() => {
    if (currentIndex === null || currentIndex === -1) return;
    if (!wordRefs.current[currentIndex] || !containerRef.current) return;

    const parentRect = containerRef.current.getBoundingClientRect();
    const activeRect = wordRefs.current[currentIndex].getBoundingClientRect();

    const newRect = {
      x: activeRect.left - parentRect.left,
      y: activeRect.top - parentRect.top,
      width: activeRect.width,
      height: activeRect.height,
    };

    setFocusRect(newRect);
    generateParticles(newRect);
  }, [currentIndex, words.length]);

  const handleMouseEnter = (index) => {
    if (manualMode) {
      setLastActiveIndex(index);
      setCurrentIndex(index);
    }
    setIsHovered(true);
  };

  const handleMouseLeave = () => {
    if (manualMode) {
      setCurrentIndex(lastActiveIndex);
    }
    setIsHovered(false);
  };

  return (
    <div 
      className="min-h-screen flex items-center justify-center p-8 relative overflow-hidden"
      style={{ 
        background: currentTheme.bg,
        fontFamily: "'Orbitron', 'Courier New', monospace"
      }}
    >
      {/* Animated background grid */}
      <div className="absolute inset-0 opacity-10">
        <div 
          className="w-full h-full"
          style={{
            backgroundImage: `
              linear-gradient(${currentTheme.textColor} 1px, transparent 1px),
              linear-gradient(90deg, ${currentTheme.textColor} 1px, transparent 1px)
            `,
            backgroundSize: '30px 30px',
            animation: 'gridMove 20s linear infinite'
          }}
        />
      </div>

      {/* Floating particles */}
      <div className="absolute inset-0 pointer-events-none">
        {particles.map(particle => (
          <div
            key={particle.id}
            className="absolute w-1 h-1 rounded-full"
            style={{
              left: particle.x,
              top: particle.y,
              backgroundColor: currentTheme.textColor,
              opacity: particle.life,
              boxShadow: `0 0 4px ${currentTheme.textColor}`,
              transform: `scale(${particle.life})`
            }}
          />
        ))}
      </div>

      <div
        className="relative flex gap-8 justify-center items-center flex-wrap max-w-6xl mx-auto"
        ref={containerRef}
        style={{
          perspective: '1000px',
          transformStyle: 'preserve-3d'
        }}
      >
        {words.map((word, index) => {
          const isActive = index === currentIndex;
          return (
            <motion.span
              key={index}
              ref={(el) => (wordRefs.current[index] = el)}
              className="relative font-bold text-4xl md:text-6xl cursor-pointer select-none"
              style={{
                color: isActive ? currentTheme.textColor : '#666',
                filter: manualMode
                  ? isActive
                    ? `blur(0px) brightness(1.2)`
                    : `blur(${blurAmount}px) brightness(0.5)`
                  : isActive
                    ? `blur(0px) brightness(1.2)`
                    : `blur(${blurAmount}px) brightness(0.5)`,
                textShadow: isActive ? currentTheme.shadowColor : 'none',
                transition: `all ${animationDuration}s cubic-bezier(0.4, 0, 0.2, 1)`,
                transform: isActive ? 'translateZ(20px) scale(1.1)' : 'translateZ(0) scale(1)',
                WebkitTextStroke: isActive ? `1px ${currentTheme.accentColor}` : 'none'
              }}
              animate={{
                rotateX: isActive ? [0, 5, 0] : 0,
                rotateY: isActive ? [0, -2, 2, 0] : 0,
              }}
              transition={{
                duration: animationDuration,
                repeat: isActive ? Infinity : 0,
                repeatType: "reverse"
              }}
              onMouseEnter={() => handleMouseEnter(index)}
              onMouseLeave={handleMouseLeave}
              whileHover={{ 
                scale: 1.05,
                rotateZ: Math.random() * 4 - 2
              }}
            >
              {word}
              
              {/* Active word effects */}
              {isActive && (
                <>
                  {/* Scanning line effect */}
                  <motion.div
                    className="absolute inset-0 pointer-events-none"
                    initial={{ opacity: 0 }}
                    animate={{ opacity: [0, 1, 0] }}
                    transition={{ duration: 2, repeat: Infinity }}
                  >
                    <div 
                      className="absolute w-full h-0.5 bg-gradient-to-r from-transparent via-current to-transparent"
                      style={{
                        background: `linear-gradient(90deg, transparent, ${currentTheme.textColor}, transparent)`,
                        animation: 'scanLine 2s ease-in-out infinite'
                      }}
                    />
                  </motion.div>

                  {/* Glitch effect */}
                  <motion.div
                    className="absolute inset-0 pointer-events-none text-red-500 opacity-30"
                    animate={{
                      x: [0, 2, -2, 0],
                      opacity: [0, 0.3, 0, 0.2, 0]
                    }}
                    transition={{
                      duration: 0.2,
                      repeat: Infinity,
                      repeatDelay: 3
                    }}
                  >
                    {word}
                  </motion.div>
                </>
              )}
            </motion.span>
          );
        })}

        {/* Enhanced focus indicator */}
        <AnimatePresence>
          {currentIndex >= 0 && (
            <motion.div
              className="absolute pointer-events-none"
              initial={{ opacity: 0, scale: 0.8 }}
              animate={{
                x: focusRect.x - 20,
                y: focusRect.y - 20,
                width: focusRect.width + 40,
                height: focusRect.height + 40,
                opacity: 1,
                scale: 1,
                rotate: [0, 1, -1, 0]
              }}
              exit={{ opacity: 0, scale: 0.8 }}
              transition={{
                duration: animationDuration,
                rotate: { duration: 4, repeat: Infinity }
              }}
              style={{
                background: `linear-gradient(45deg, transparent 30%, ${glowColor} 50%, transparent 70%)`,
                borderRadius: '8px',
                filter: 'blur(1px)'
              }}
            >
              {/* Corner brackets with enhanced effects */}
              {[
                { pos: 'top-left', style: { top: '-12px', left: '-12px', borderTop: `4px ${currentTheme.borderStyle} ${borderColor}`, borderLeft: `4px ${currentTheme.borderStyle} ${borderColor}` }},
                { pos: 'top-right', style: { top: '-12px', right: '-12px', borderTop: `4px ${currentTheme.borderStyle} ${borderColor}`, borderRight: `4px ${currentTheme.borderStyle} ${borderColor}` }},
                { pos: 'bottom-left', style: { bottom: '-12px', left: '-12px', borderBottom: `4px ${currentTheme.borderStyle} ${borderColor}`, borderLeft: `4px ${currentTheme.borderStyle} ${borderColor}` }},
                { pos: 'bottom-right', style: { bottom: '-12px', right: '-12px', borderBottom: `4px ${currentTheme.borderStyle} ${borderColor}`, borderRight: `4px ${currentTheme.borderStyle} ${borderColor}` }}
              ].map((corner, i) => (
                <motion.div
                  key={corner.pos}
                  className="absolute w-6 h-6 rounded-sm"
                  style={{
                    ...corner.style,
                    filter: `drop-shadow(0 0 8px ${borderColor}) drop-shadow(0 0 16px ${borderColor})`,
                    animation: `pulse${i} 2s ease-in-out infinite`
                  }}
                  animate={{
                    scale: [1, 1.1, 1],
                    opacity: [0.8, 1, 0.8]
                  }}
                  transition={{
                    duration: 2,
                    repeat: Infinity,
                    delay: i * 0.2
                  }}
                />
              ))}

              {/* Energy pulse rings */}
              <motion.div
                className="absolute inset-0 rounded-lg border-2 opacity-60"
                style={{ borderColor: currentTheme.accentColor }}
                animate={{
                  scale: [1, 1.2, 1],
                  opacity: [0.6, 0.2, 0.6]
                }}
                transition={{
                  duration: 2,
                  repeat: Infinity
                }}
              />
              
              <motion.div
                className="absolute inset-0 rounded-lg border border-dashed opacity-40"
                style={{ borderColor: borderColor }}
                animate={{
                  scale: [1, 1.3, 1],
                  opacity: [0.4, 0.1, 0.4],
                  rotate: [0, 180, 360]
                }}
                transition={{
                  duration: 4,
                  repeat: Infinity
                }}
              />
            </motion.div>
          )}
        </AnimatePresence>
      </div>

      {/* HUD-style info display */}
      <div className="absolute top-8 left-8 text-sm opacity-60" style={{ color: currentTheme.textColor, fontFamily: 'monospace' }}>
        <div>FOCUS: {currentIndex + 1}/{words.length}</div>
        <div>MODE: {manualMode ? 'MANUAL' : 'AUTO'}</div>
        <div>THEME: {theme.toUpperCase()}</div>
        <div>STATUS: {isHovered ? 'ACTIVE' : 'SCANNING'}</div>
      </div>

      <style jsx>{`
        @import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;700;900&display=swap');
        
        @keyframes gridMove {
          0% { transform: translate(0, 0); }
          100% { transform: translate(30px, 30px); }
        }
        
        @keyframes scanLine {
          0% { top: 0%; opacity: 0; }
          50% { opacity: 1; }
          100% { top: 100%; opacity: 0; }
        }
        
        @keyframes pulse0 {
          0%, 100% { transform: scale(1) rotate(0deg); }
          50% { transform: scale(1.1) rotate(90deg); }
        }
        
        @keyframes pulse1 {
          0%, 100% { transform: scale(1) rotate(0deg); }
          50% { transform: scale(1.1) rotate(-90deg); }
        }
        
        @keyframes pulse2 {
          0%, 100% { transform: scale(1) rotate(0deg); }
          50% { transform: scale(1.1) rotate(180deg); }
        }
        
        @keyframes pulse3 {
          0%, 100% { transform: scale(1) rotate(0deg); }
          50% { transform: scale(1.1) rotate(-180deg); }
        }
      `}</style>
    </div>
  );
};

export default TrueFocus3D;